section.films {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: baseline; }
  section.films .film.selected {
    max-height: 700px; }
  section.films .film {
    flex: 0 1 12%;
    min-height: 470px;
    position: relative;
    padding: 16px;
    border-radius: 6px;
    background: #fff;
    margin: 8px;
    max-height: 470px;
    transition: max-height 300ms cubic-bezier(0.4, 0, 0.2, 1) 10ms;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12); }
    section.films .film img {
      max-width: 210px; }
    section.films .film .title {
      display: flex;
      justify-content: space-between; }
      section.films .film .title span.name {
        white-space: nowrap;
        overflow: hidden;
        width: 181px;
        text-overflow: ellipsis; }
    section.films .film .rating svg {
      color: #F5C518; }
    section.films .film .genres {
      height: 50px;
      border-radius: 4px;
      padding: 3px;
      box-shadow: 0px 0px 5px 0px rgba(34, 60, 80, 0.2) inset; }
    section.films .film .actions {
      display: flex;
      height: 20px;
      width: 210px;
      justify-content: space-between;
      position: absolute;
      bottom: 14px; }
      section.films .film .actions button {
        background: #2196f3;
        min-width: 40px;
        border-radius: 4px; }
      section.films .film .actions button:last-child {
        background: #f50057; }

.chip {
  margin: 2px 4px;
  color: #fff;
  border: none;
  cursor: default;
  display: inline-flex;
  outline: 0;
  padding: 0;
  font-size: 0.8125rem;
  box-sizing: border-box;
  transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  align-items: center;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  white-space: nowrap;
  border-radius: 16px;
  vertical-align: middle;
  justify-content: center;
  text-decoration: none;
  background-color: #616161; }
  .chip span {
    padding-left: 8px;
    padding-right: 8px; }
  .chip button {
    background: 0;
    border: 0;
    padding: 4px;
    box-shadow: none; }
