.comment {
  text-align: center;
  max-width: 210px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  flex-direction: row;
  max-height: 230px;
  overflow-y: auto;
  justify-content: center;
  margin-bottom: 32px; }
  .comment .wrapper {
    flex: 0 1 100%;
    display: flex; }
  .comment button {
    border: 0;
    background: #616161;
    color: #fff;
    border-radius: 8px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .comment textarea {
    width: 100%;
    resize: none;
    border: 0;
    box-shadow: 0px 0px 5px 0px rgba(34, 60, 80, 0.4) inset;
    border-radius: 8px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  .comment textarea:focus {
    outline: 0; }
  .comment span {
    margin: 2px 0 8px 0;
    flex: 0 1 100%; }
  .comment .list {
    flex: 0 1 100%; }
    .comment .list .value {
      box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
      padding: 8px;
      text-align: left;
      padding-left: 24px;
      margin-bottom: 10px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start; }
      .comment .list .value span {
        white-space: break-spaces; }
      .comment .list .value button {
        border-radius: 8px; }
